.form {
  display: flex;
  align-items: flex-end;
  gap: 18px;
  width: 859px;
  margin: 0 auto;
  margin-bottom: 50px;
}

.label {
  font-size: 14px;
  font-weight: 500;
  line-height: 1.29;
  color: var(--primary-color-grey);
  margin-bottom: 8px;
}

.labelwrap {
  display: flex;
  flex-direction: column;
}

.inputwrap {
  display: flex;
}

.button {
  height: 48px;
  padding: 14px 44px;
  border-radius: 12px;

  background: var(--primary-color-blue);
  color: var(--primary-color-white);

  font-size: 14px;
  font-weight: 600;
}

.button:hover {
  background-color: var(--secondary-color-blue);
}

.inputContainer {
  position: relative;
}

input {
  color: var(--primary-color-black);
  font-size: 18px;
  font-weight: 500;
  line-height: 1.11;

  height: 48px;
  border: none;
  outline: none;
  background: var(--secondary-color-grey);
}

.inputBrand {
  border-radius: 14px;
  width: 224px;
  padding: 14px 89px 14px 18px;
}

.inputPrice {
  border-radius: 14px;
  width: 125px;
  padding: 14px 18px;
}

.inputFrom {
  border-right: 1px solid rgba(138, 138, 137, 0.2);
  border-radius: 14px 0px 0px 14px;
  background: var(--secondary-color-grey);
  width: 160px;
  padding: 14px 24px 14px 75px;
}

.inputTo {
  width: 160px;
  border-radius: 0px 14px 14px 0px;
  background: rgb(247, 247, 251);
  padding: 14px 24px 14px 53px;
}

input::placeholder {
  color: var(--primary-color-black);

  font-size: 18px;
  font-weight: 500;
  line-height: 1.11;
}

.icon {
  cursor: pointer;
  stroke: var(--primary-color-black);
  position: absolute;
  top: 50%;
  right: 14px;
  transform: translateY(-50%);
}

.upperLabel {
  position: absolute;
  top: 14px;
  left: 24px;

  color: var(--primary-color-black);

  font-size: 18px;
  font-weight: 500;
  line-height: 1.11;
}

.inputLabelWrap {
  position: relative;
}

/* For WebKit browsers */
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* For Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}
